// Check page access
export const checkPageAccess = (user, componet) => {
  const path = componet.path;
  const location = componet.location;
  const splitPath = path.split("/");
  const role = user.role ? user.role : 'guest';
  let page = user.page;
  // u will get path name here from url
  console.log(splitPath)
  // u will get all info of user
  switch (role) {
    case userRoles.superUser:
      return true;
    case userRoles.admin:
      return page.indexOf(splitPath[1]) >= 0;
    case userRoles.seller:
      return page.indexOf(splitPath[1]) >= 0;
    case userRoles.member:
      return page.indexOf(splitPath[1]) >= 0;
    case userRoles.guest:
      return page.indexOf(splitPath[1]) >= 0;
    default:
      return false;
  }
};

/**
 * Get Redirect Page
 * Redirect when logged in
 */
export const redirectPage = (user) => {
  if(user){
    const role = user.role ? user.role : 'guest'
    const page = user.page;

    switch(role){
      case userRoles.superUser:
        return 'home';
      case userRoles.admin:
        return 'admin';
      case userRoles.member:
        return 'home';
      case userRoles.seller:
        return 'products';
      case userRoles.guest:
        return 'home';
      default:
        return 'noaccess'
    }
  } else {
    return 'noaccess'
  }
}


  /**
   * Get Redirect page
   */
  export const getCurrentListPagePath = (user, match) => {
    if(user){
      const path = match.path.split('/')[1];
      const urlPath = window.location.href.split('/');
      const urlPathLeng = urlPath.length;

      if(path === 'profile' || path === 'client')
        return redirectPage(user);
      else{
        switch(path) {
          case 'surveys':
            return 'surveys/manage';
          case 'people':
            const pIndex = urlPath.indexOf('people') + 1;
            if(urlPathLeng > pIndex){
              if(urlPath[pIndex] === 'demographics')
              return 'people'
              else
              return `people/${urlPath[pIndex]}?reset=true`;
            } else{
              return 'people';
            }
          case 'reports':
            if(urlPathLeng === 5)
              return 'reports'
            else
              return `reports/${urlPath[urlPathLeng - 1]}`
          case 'actions':
            return 'actions';
          case 'spcaller':
              return 'spcaller?reset=true'
          default:
              return path;
        }
      }
    } else{
      return 'noaccess'
    }
  }