import React, { useEffect, useState } from 'react'
import Grid from '@material-ui/core/Grid';
import HomeStyle from './HomeStyle'
import { withStyles } from "@material-ui/core/styles";
import TypographyCustom from '../../components/Typogrphy/TypographyCustom';
import { Card, CardActionArea, CardContent, CircularProgress } from '@material-ui/core';
import Carousel from 'react-material-ui-carousel'
import { Content } from 'antd/lib/layout/layout';

function CategorizedProducts(props) {
  const { classes, data } = props;

  useEffect(() => {
    setProducts(data)
  }, [data])

  const [products, setProducts] = useState()

  const handleImageLoader = (item) => {
    let testProducts = [];
    products.filter(d => d._id === item._id)[0].isLoad = true
    testProducts = [
      ...products
    ]
    setProducts(testProducts)
  }

  const generateProducts = () => {
    let row = [];
    if(products){
      console.log(products.length)
    for(let i=0; i < products.length-1;){
      let content = [];

      for (let j = 0; j < 3; j++) {
        if(i < products.length-1){
        console.log(i)
        console.log(products[i])
        content.push(
          <Grid className='test' item xs={3} key={j}>

            <Card >

              <img className={classes.img} src={products[i].img} alt={products[i].name} onLoad={() => handleImageLoader(products[i])} />
              <p>{products[i].name}</p>
              {!products[i].isLoad && <CircularProgress className={classes.productProgress} />}
            </Card>
          </Grid>
        )
        i++;
        }
      }
      
      row.push(<Grid container xs={12} spacing={2} justify='space-around' direction='row'>
      {content}
        </Grid>)
    }
  }
    console.log(row)
    return row
  }
  return (
    <Grid item className={classes.root} xs={12}>
        <Carousel xs={12} interval={10000} timeout={1000} animation='fade' indicators={true} navButtonsAlwaysVisible={true} className={classes.carousel}>
          {
            generateProducts()
          }
        </Carousel>
        </Grid>
      
  )
}

export default withStyles(HomeStyle)(CategorizedProducts);
