import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

// Import Components
import ButtonComponent from "../../components/ButtonComponent/ButtonComponent";

// Import constants
import { userRoles, userAccess } from "../../config/pageAccess";

function HeaderNavigations (props) {
  // Get Props
  const { user, match } = props;

  // Get Active Header
  const page = match.path;

  return (
    <React.Fragment>
      {(user.role === userRoles.superadmin || user.page.indexOf(userAccess.surveys) >= 0) &&
        <ButtonComponent
          title={"Surveys"}
          color={"default"}
          size={"large"}
          component={Link}
          to="/surveys"
          type={"button"}
          variant={"text"}
          pageClassName={`navLink ${page === "/surveys" ? "activeHeader" : ""}`}
          componentClassName={"header"}
        ></ButtonComponent>
      }

      { (user.role === userRoles.superadmin || user.page.indexOf(userAccess.people) >= 0) &&
        <ButtonComponent
          title={"People"}
          color={"default"}
          size={"large"}
          component={Link}
          to="/people"
          type={"button"}
          variant={"text"}
          pageClassName={`navLink ${page === "/people" ? "activeHeader" : ""}`}
          componentClassName={"header"}
        ></ButtonComponent>
      }

      { (user.role === userRoles.superadmin || user.page.indexOf(userAccess.reports) >= 0) &&
        <ButtonComponent
          title={"Reports"}
          color={"default"}
          size={"large"}
          component={Link}
          to="/reports"
          type={"button"}
          variant={"text"}
          componentClassName={"header"}
          pageClassName={`navLink ${page === "/reports" ? "activeHeader" : ""}`}
        ></ButtonComponent>
      }

      { (user.role === userRoles.superadmin || user.page.indexOf(userAccess.actions) >= 0) &&
        <ButtonComponent
          title={"Action"}
          color={"default"}
          size={"large"}
          component={Link}
          to="/actions"
          type={"button"}
          variant={"text"}
          componentClassName={`${"header"}`}
          pageClassName={`navLink ${page === "/actions" ? "activeHeader" : ""}`}
        ></ButtonComponent>
      }

      {(user.role === userRoles.superadmin || user.role === userRoles.calleradmin || user.role === userRoles.calleruser || user.page.indexOf(userAccess.spcaller) >= 0) &&
        <ButtonComponent
          title={"Call Site"}
          color={"default"}
          size={"large"}
          component={Link}
          to="/spcaller"
          type={"button"}
          variant={"text"}
          pageClassName={`navLink ${page === "/spcaller" ? "activeHeader" : ""}`}
          componentClassName={`${"header"}`}
        ></ButtonComponent>
      }
    </React.Fragment>
  );
}

// default props
HeaderNavigations.defaultProps = {
  classes: {},
  match: {},
  user: {}
};

// prop types
HeaderNavigations.propTypes = {
  classes: PropTypes.object,
  match: PropTypes.object,
  user: PropTypes.object
};

/**
 * Export Container
 */
export default HeaderNavigations;
