import React from "react";
import { useSelector, useDispatch } from "react-redux";
import PropTypes from "prop-types";
import Snackbar from "@material-ui/core/Snackbar";
import Slide from "@material-ui/core/Slide";
import { withStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";

// Import Actions
import { alertAction } from "../../redux/actions";

// Import Styles
import SnackBarStyle from "./SnackBarStyle";

function TransitionDown (props) {
  return <Slide {...props} direction="down" />;
}

function SnackBarCustom (props) {
  /**
   * Redux Action and Reducer
   */
  const dispatch = useDispatch();
  const alert = useSelector((state) => state.alert);

  /**
   *
   * @param {*} event
   * @param {*} reason
   */
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    dispatch(alertAction.clear());
  };

  /**
   *
   */
  const handleExited = () => {
    dispatch(alertAction.clear());
  };

  /**
   *
   */
  const { classes } = props;
  const { type, message } = alert;

  return (
    <Snackbar
      key={type || undefined}
      anchorOrigin={{
        vertical: "top",
        horizontal: "right"
      }}
      ContentProps={{
        classes: {
          root: classes[type]
        }
      }}
      open={!!type}
      TransitionComponent={TransitionDown}
      autoHideDuration={3000}
      onClose={handleClose}
      onExited={handleExited}
      message={<span dangerouslySetInnerHTML={{ __html: message }}></span>}
      action={
        <React.Fragment>
          <IconButton
            aria-label="close"
            color="inherit"
            className={classes.close}
            onClick={handleClose}
          >
            <CloseIcon />
          </IconButton>
        </React.Fragment>
      }
    />
  );
}

// default props
SnackBarCustom.defaultProps = {
  classes: {}
};

// prop types
SnackBarCustom.propTypes = {
  classes: PropTypes.object
};

// export component
export default withStyles(SnackBarStyle)(SnackBarCustom);
