import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { useSelector, useDispatch } from "react-redux";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";

// // Import Styles and Image
import AuthenticationLayoutStyles from "./AuthenticationLayoutStyle";
import Logo from "../../assets/images/logo_text.png";
import Zoom from '@material-ui/core/Zoom';

// Import Components
import TypographyComponent from "../../components/Typogrphy/TypographyCustom";

// Import Cofig Constants
import { authenticationConstants } from "../../config/constants";
import { stateAction } from "../../redux/actions/stateAction";
import { uiConstant } from "../../redux/constants";

function AuthenticationLayout(props) {
  const { classes } = props;
  const dispatch = useDispatch();
  const { transition } = useSelector(state => state.UI)
  useEffect(()=>{
    dispatch(stateAction.update(uiConstant.TRANSITION, true))
  }, [dispatch])
  return (
    <Grid xs={12} container direction='row' className={classes.container} justify='center' align='center' alignContent='center'>
      <Grid item xs={12} sm={8} md={8} lg={6} xl={6}>
        <Grid item xs={12} sm={8} md={8} lg={6} xl={6}>
          <TypographyComponent
            title='Title'
            variant='h1'
            color='secondary'
            align='center'
          />
        </Grid>
        <Zoom in={true} style={{ transitionDelay: '500ms' }}>
        <Grid item xs={12} sm={8} md={8} lg={6} xl={6} className={classes.auth_container}> {props.children} </Grid>
        </Zoom>
      </Grid>
    </Grid>
  );
}

// prop types
AuthenticationLayout.propTypes = {
  classes: PropTypes.object,
  children: PropTypes.element.isRequired
};

export default withStyles(AuthenticationLayoutStyles)(AuthenticationLayout);
