import React, { useEffect, useState } from 'react'
import Grid from '@material-ui/core/Grid';
import HomeStyle from './HomeStyle'
import { withStyles } from "@material-ui/core/styles";
import TypographyCustom from '../../components/Typogrphy/TypographyCustom';
import { Card, CardActionArea, CardContent, CircularProgress } from '@material-ui/core';
import Carousel from 'react-material-ui-carousel'

function FeaturedProducts(props) {
  const { classes } = props;
  const [products, setProducts] = useState([
    {
      _id: '1',
      img: 'https://drive.google.com/uc?export=view&id=1hG41CEMEc05tc8N-UvdGZoyoZMuJ1sZF',
      title: 'Product 1',
      description: 'description'
    },
    {
      _id: '2',
      img: 'https://drive.google.com/uc?export=view&id=1RCAUFoqPT2kXD0gnmEVpK5nEXbmFLDyQ',
      title: 'Product 2',
      description: 'description'
    },
    {
      _id: '3',
      img: 'https://drive.google.com/uc?export=view&id=1TRoPbEda47DKF2ve-YtfxXdZQTl65OCU',
      title: 'Product 3',
      description: 'description'
    },
    {
      _id: '4',
      img: 'https://drive.google.com/uc?export=view&id=1qkcFrQ9m1Ys7aL6pBZ37vXaexmYJN3Y_',
      title: 'Product 4',
      description: 'description'
    }
])


  const handleImageLoader = (item) => {
    let testProducts = [];
    products.filter(d=> d._id === item._id)[0].isLoad = true
    testProducts = [
      ...products
    ]
    setProducts(testProducts)
  }
  return (
    <Grid container xs={12} className={classes.featureContainer}>
    <Grid item xs={12}>
      <TypographyCustom 
        title='Featured Items'
      />
    </Grid>
    <Grid item className={classes.root} xs={12}>
    <Carousel interval={5000} timeout={300} animation='slide' indicators={false} navButtonsAlwaysVisible={true} className={classes.carousel}>
            {
                products.map( (item, i) => (
                  <Card >   
      <CardActionArea>
      <img className={classes.img} src={item.img} alt={item.title} onLoad={ ()=>handleImageLoader(item)} />

        { !item.isLoad && <CircularProgress className={classes.progress} />}
<CardContent>
          <TypographyCustom 
            title={item.title}
            variant='h2'
          />
        </CardContent>
      </CardActionArea>
          </Card>
                ) )
            }
        </Carousel>
    </Grid>
    </Grid>
  )
}

export default withStyles(HomeStyle)(FeaturedProducts);
