import React, { useEffect, useState } from 'react'
import Grid from '@material-ui/core/Grid';
import HomeStyle from './HomeStyle'
import { withStyles } from "@material-ui/core/styles";
import FeaturedProducts from './FeaturedProducts';
import CategorizedProducts from './CategorizedProducts';
import { products } from './generated'
import TypographyCustom from '../../components/Typogrphy/TypographyCustom';
function Home(props) {
  const { classes } = props;
  const [sortedList, setSortedList ]= useState(null);
  useEffect(()=>{
    let updatedSortList = {};
    console.log(products.updated)
    products.updated.map(d=>{
      if(d.category in updatedSortList)
      updatedSortList = {
        ...updatedSortList,
        [d.category]: [
          ...updatedSortList[d.category],
          d
        ]
      }
      else
      updatedSortList = {
          ...updatedSortList,
          [d.category]: [
            d
          ]
        }
    })
    setSortedList(updatedSortList)
  }, [])

  const generateProductCategories = () => {
    return ["smartphone", "laptop", "consoles", "accessories", "Clothes"].map(d=>(
      <Grid container xs={12} className={classes.featureContainer}>
        <TypographyCustom 
          title={d}
          variant='h2'
        />
      <CategorizedProducts data={sortedList[d]}/>
      </Grid>
    )
    )
  }
  return (
    <Grid container>
      <FeaturedProducts />
      {
        sortedList && generateProductCategories()
      }
    </Grid>
  )
}

export default withStyles(HomeStyle)(Home);
