/* eslint-disable dot-notation */
import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";

// Imports Styles
import TypographyStyle from "./TypographyStyle";

/**
 * Typography Component
 * @param {*} props
 */
function TypographyCustom (props) {
  const { classes, required, className, color, title, variant, children, align } = props;
  const clsName = required === true ? `${className} ${classes["required"]}` : className;

  return (
    <Typography
      variant={variant}
      className={clsName}
      align={align}
      color={color}
    > {title}{children}</Typography>
  );
}

// default props
TypographyCustom.defaultProps = {
  classes: {},
  title: "",
  variant: "h2",
  className: "",
  children: "",
  color: "",
  align: "left",
  required: false
};

// prop types
TypographyCustom.propTypes = {
  classes: PropTypes.object,
  title: PropTypes.string.isRequired,
  variant: PropTypes.string.isRequired,
  className: PropTypes.string,
  children: PropTypes.any,
  color: PropTypes.string,
  align: PropTypes.string,
  required: PropTypes.bool
};

/** Export Component */
export default withStyles(TypographyStyle)(TypographyCustom);
