import { baseAction } from "./baseAction";

const { success } = baseAction;

// Export All Base Action Functions
export const stateAction = {
  update
};

/**
 * Update Reducer Data
 * @param type
 * @param data
 */
function update (type, data, successCallback = ()=>{}) {
  return (dispatch) => {
    dispatch(success(type, data));
    successCallback()
  }
};